    background-color: rgba(250, 250, 250, .3);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 18px;
    font-weight: 400;
    padding: 0 30px;
    opacity: .9;
    position: fixed;
    z-index: 9999;
    left: 0;
    right: 0;
}